  &:hover {
    cursor: pointer;
  }
}

.contenedor-padre {
  display: grid;
  width: 100%;
  grid-template-columns: 30px 1fr 30px;
  align-items: center;
  gap: 15px;
  .scroll{
    display: flex;
    transition: transform 0.3s ease; /* Agrega una transicin suave al desplazamiento */
    width: 100%;
  }
  .btn-fijo {
    background: transparent;
    border: none;
    color: #707070;
    z-index: 1;
    transition: all ease .2s;

    &:disabled {
      cursor: not-allowed;
      opacity: 50%;
    }
  }
}


